a {
  text-decoration: none;
}

.button {
	box-shadow: 0px 10px 14px -7px #276873;
	background: linear-gradient(to bottom, #827397 5%, #363062 100%);
	background-color: #827397;
	border-radius: 8px;
	display: inline-block;
	cursor: pointer;
	color: #e9d8dd;
	font-family: Arial;
	font-size: 20px;
	font-weight: bold;
	padding: 13px 32px;
	text-shadow: 0px 1px 0px #3d768a;
}